@import url(https://fonts.googleapis.com/css2?family=Lexend:wght@300;400;500;600;700&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

::selection{background-color:#30f;color:#fff}.card{max-width:100%;min-width:250px;flex-grow:1;flex-basis:250px;margin-right:16px;margin-bottom:48px;display:flex;align-items:center;flex-direction:column;border-radius:20px;padding:18px 12px 24px 12px;transition:.3s;box-shadow:0px 43px 31px rgba(0,0,0,0.02),0px 17.9644px 12.9511px rgba(0,0,0,0.0143771),0px 9.60461px 6.92426px rgba(0,0,0,0.0119221),0px 5.38427px 3.88168px rgba(0,0,0,0.01),0px 2.85954px 2.06153px rgba(0,0,0,0.00807786),0px 1.18992px 0.85785px rgba(0,0,0,0.00562291)}.card__title{font-size:35px;text-align:center;margin-top:0;color:#545454}.card__subtitle{text-align:center;margin-top:0;color:#909090}.card__saveBtn{border:none;cursor:pointer;font-weight:500;color:#545454;background:none;border:3px solid rgba(187,187,187,0.6);height:50px;width:50px;display:flex;align-items:center;justify-content:center;height:50px;border-radius:15px}.card__saveBtn>span{font-weight:500}.card__saveBtn--saved svg{color:#30f;fill:#30f}.tabs{margin-top:100px}@media only screen and (max-width: 510px){.tabs{margin-top:64px}}.tabs__nav{display:flex;list-style:none;padding:0}.tabs__nav__tab__btn{all:unset;cursor:pointer;font-size:18px;padding:8px 16px;transition:.3s;position:relative;color:#545454}.tabs__nav__tab__btn:before{background:none repeat scroll 0 0 transparent;transition:width 0.3s ease 0s, left 0.3s ease 0s;width:0;height:3px;background-color:#30f;top:-4px;left:50%;position:absolute;content:''}.tabs__nav__tab__btn:hover:before{width:100%;left:0}.tabs__content__wrapper{display:none;width:100%;flex-wrap:wrap}.tabs__nav__tab--active .tabs__nav__tab__btn{color:#30f}.tabs__nav__tab--active .tabs__nav__tab__btn:before{width:100%;left:0}body .tabs__content__wrapper--active{display:flex}.tabMsg{text-align:center;margin:auto;max-width:400px;font-size:1.2em}.tabMsg__title{color:#545454}.tabMsg__paragraph{color:#909090}.form{display:flex;align-items:center;flex-wrap:wrap;position:relative;max-width:-webkit-max-content;max-width:max-content;margin-top:48px}.form__searchbox{position:relative;flex-shrink:3;width:400px}.form__searchbox__btn{border:none;cursor:pointer;font-weight:500;background-color:#30f;color:#fff;min-height:60px;padding:8px 24px;border-radius:15px;font-size:18px;position:absolute;top:50%;-webkit-transform:translate(-100%, -50%);transform:translate(-100%, -50%)}.form__searchbox__btn>span{font-weight:500}.form__searchbox__input{min-height:50px;background-color:#F7F8FB;border-radius:15px;border:none;padding:12px;width:0;min-width:100%;font-size:18px;color:#545454;transition:.4s;padding-right:124px}.form__searchbox__input:focus{outline:none;background-color:#f0f2f8}@media only screen and (max-width: 510px){.form__searchbox{min-width:-webkit-fill-available}}.form__btn{border:none;cursor:pointer;font-weight:500;color:#545454;background:none;border:3px solid rgba(187,187,187,0.6);height:50px;width:50px;display:flex;align-items:center;justify-content:center;border-radius:15px;min-height:60px;width:60px;margin-left:16px;font-size:18px}.form__btn>span{font-weight:500}@media only screen and (max-width: 510px){.form__btn{min-width:100%;margin-top:24px;margin-left:0}}.form__btn__txt{margin-left:8px;display:none}@media only screen and (max-width: 510px){.form__btn__txt{display:inline}}*{box-sizing:border-box;font-family:'Lexend'}ul{padding-left:0}button{cursor:pointer}.link{color:#30f}.header__title{font-size:50px;color:#545454}@media only screen and (max-width: 600px){.header__title{font-size:35px}}.nav{width:100%;min-height:70px;display:flex;align-items:center;justify-content:center}.nav__wrapper{max-width:1160px;width:100%;padding:0 16px}.main{max-width:1160px;width:100%;padding:0 16px}.footer{margin-top:auto;min-height:60px}.app{min-height:100vh;width:100vw;display:flex;align-items:center;justify-content:flex-start;flex-direction:column}

